.post__comment-list {
  border: 1px solid #f1f1f1;
}

.post__comment-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 10px 10px;

  background-color: #000;
  color: #fff;
}

.post__comment-list-header h4 {
  margin: 0;
  padding: 0;
}

.comments_button {
  margin: 0;
  padding: 5px;
  min-height: 20px;

  background: none;
  color: #d1d1d1;
  border: none;

  transition: all 1s;
}

.comments_button:hover {
  color: #fff;
  cursor: pointer;
}
