.post-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  position: fixed;
  top: 70px;
  right: 20px;

  padding: 15px 25px;
  background-color: rgb(26, 26, 26);
  color: #fff;
  text-transform: uppercase;
  border: none;
}

.button:hover {
  background-color: #000;
  cursor: pointer;
}

